import { useState } from 'react'

import styled from 'styled-components'

import Input from './Components/Input'
import Title from './Components/Title'
import SubmitButton from './Components/SubmitButton'
import arrow_left_black from './images/chevron-left-black.svg'
import arrow_left_white from './images/chevron-left-white.svg'

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
  padding: 20px;
  transform: ${props => props.active ? 'translateX(-50%)' : 'translateX(200px)'};
  opacity: ${props => props.active ? '1' : '0'};
`;

const ToSignIn = styled.div`
  font-size: 14px;
  padding: 4px 6px 4px 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  position: relative;
  background-color: #fff;
  align-self: flex-start;
  cursor: pointer;
  margin-top: 10px;
  transition: box-shadow .2s linear;
`;

const LeftArrow = styled.img`
  transform: translateX(0) 
`;

const ButtonText = styled.p`
  font-size: 16;
  margin: 0 0 0 5px;
`;

function Signup({ active, handleConnectDisplayChange, handleOnSubmitSignup, theme }) {

  const [email, setEmail] = useState('')
  const [password1, setPassword1] = useState('')
  const [password2, setPassword2] = useState('')

  return(
    <Container className="form" active={active}>
      <Title value="Create your account" />
        <Input 
          originalType="text"
          placeholder="email"
          autoComplete="off"
          value={email}
          onChange={setEmail}
        />
        <Input
          originalType="password"
          placeholder="Password"
          autoComplete="off"
          value={password1}
          onChange={setPassword1}
        />
        <Input 
          originalType="password"
          placeholder="Confirm password"
          autoComplete="off"
          value={password2}
          onChange={setPassword2}
        />
      <SubmitButton value='Confirm' type="submit" onClick={() => handleOnSubmitSignup(email, password1, password2)}/>
      <ToSignIn className="button" onClick={() => handleConnectDisplayChange()}>
        <LeftArrow src={theme === 'light' ? arrow_left_black : arrow_left_white} alt="Icon to SignIn" />
        <ButtonText className="text">Sign In</ButtonText>
      </ToSignIn>
    </Container>
  )
}

export default Signup