import { useState } from 'react'
import styled from 'styled-components'
import Input from './Components/Input'
import Title from './Components/Title'
import arrow_right_black from './images/chevron-right-black.svg'
import arrow_right_white from './images/chevron-right-white.svg'


const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
  padding: 20px;
  transform: ${props => props.active ? 'translateX(50%)' : 'translateX(-200px)'};
  opacity: ${props => props.active ? '1' : '0'};
`;

const Button = styled.button`
  margin-top: 10px;
  padding: 5px;
  font-size: 14px;
`;

const ToSignUp = styled.div`
  font-size: 14px;
  padding: 4px 4px 4px 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  position: relative;
  align-self: flex-end;
  cursor: pointer;
  margin-top: 10px;
  transition: box-shadow .2s linear;
`;

const RightArrow = styled.img`
  transform: translateX(0) 
`;

const ButtonText = styled.p`
  font-size: 16;
  margin: 0 5px 0 0;
`;

function Signin({ active, handleConnectDisplayChange, handleOnSubmitSignin, theme }) {

  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  return(
    <Wrapper className="form" active={active}>
      <Title value="Sign In" />
      <Input 
        originalType="text"
        placeholder="email"
        autoComplete="off"
        value={email}
        onChange={setEmail}
      />
      <Input
        originalType="password"
        placeholder="Password"
        autoComplete="off"
        value={password}
        onChange={setPassword}
      />
      <a href="#">Forgot your password ?</a>
      <Button 
        onClick={() => handleOnSubmitSignin(email, password)} 
        type="submit" 
      >
        Submit
      </Button>
      <ToSignUp className="button" onClick={() => handleConnectDisplayChange()}>
        <ButtonText >Sign Up</ButtonText>
        <RightArrow src={theme === 'light' ? arrow_right_black : arrow_right_white} alt="Icon to SignUp" />
      </ToSignUp>
    </Wrapper>
  )
}

export default Signin